    total_check(){
      if(!(this.qnaList.twelveth.jQna.check || this.qnaList.twelveth.pQna.check)){
        this.qnaList.twelveth.display=true;
        return false;
      }
      var i_cnt=0;
      var e_cnt=0;
      var s_cnt=0;
      var n_cnt=0;
      var t_cnt=0;
      var f_cnt=0;
      var j_cnt=0;
      var p_cnt=0;
      
      if(this.qnaList.first.jQna.check){
        j_cnt++;
      }else if(this.qnaList.first.pQna.check){
        p_cnt++;
      }
      if(this.qnaList.second.iQna.check){
